@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
*{box-sizing:border-box;margin:0;padding:0;font-family:'Nanum Gothic', sans-serif}html,body,#root{width:100%;min-height:100%}#root{color:#ffffff;background-color:#51a9e3}#root a{text-decoration:none}

#bigWrapper{width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:130%;display:flex;flex-direction:column;justify-content:space-between;overflow:hidden}#bigWrapper .page{width:100%;min-height:100%;position:relative;padding-bottom:1.5em}#bigWrapper .page+.page{margin-top:25%}#bigWrapper .selfIntro .selfIntroContainer{width:100%;min-height:80vh;display:flex;flex-direction:column;justify-content:space-around;align-items:center;margin-top:8%}@media only screen and (min-width: 1470px){#bigWrapper .selfIntro .selfIntroContainer{flex-direction:row}}#bigWrapper .iconContainer{width:inherit;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:20% 5% 5% 5%;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-evenly;align-items:center;position:relative}@media only screen and (min-width: 1470px){#bigWrapper .iconContainer{height:100vh;padding-top:10%;flex-direction:column}}#bigWrapper .toyProject{display:flex;flex-direction:column;justify-content:center;align-items:center}@media only screen and (min-width: 991px){#bigWrapper .toyProject{flex-direction:row}}#bigWrapper .toyProject .projectContainer{width:100%;padding:calc(100% * 0.05 + 1.5em) 0;margin-bottom:5em;display:flex;flex-direction:column;justify-content:center;align-items:center}

h1{text-align:center;font-size:200%;color:#ffffff;position:absolute;top:0;left:50%;transform:translatex(-50%);z-index:2}@media only screen and (min-width: 1470px){h1{font-size:300%}}

footer{width:100%;height:30vh;padding:15%;background-color:#050586;font-size:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}@media only screen and (min-width: 566px){footer{font-size:150%}}@media only screen and (min-width: 991px){footer{font-size:200%}}@media only screen and (min-width: 991px){footer{padding:5%}}footer .line{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}footer .line img{width:1em;height:1em;float:left}footer .line p{margin-left:1.5em}footer .line+.line{margin-top:1.5em}

.card{width:80%;min-height:40vh;margin:3em 1em;padding:1.5em 1.5em;line-height:1.5em;border:1.5em solid #f1c40f;border-radius:1.5em;background-color:#ffffff}@media only screen and (min-width: 566px){.card{display:flex}}@media only screen and (min-width: 991px){.card{display:block;flex-direction:none}}.card.makeItFit{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;min-height:40vh;display:flex;flex-direction:column;justify-content:center}.card *{color:darkblue}.card img{width:80%;height:auto;float:none;display:block;margin:1.5em auto}@media only screen and (min-width: 991px){.card img{width:15vw;height:15vw;float:left;margin-right:1.5em}}.card p{font-size:120%;font-weight:700;margin:1em 1.5em}@media only screen and (min-width: 991px){.card p{margin:1em 0}}.card .projectTitle{font-size:150%;font-weight:900;margin:1em 1.5em}@media only screen and (min-width: 991px){.card .projectTitle{margin:1em 0}}.card .skillContainer{width:100%;margin:0;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;align-items:center}@media only screen and (min-width: 991px){.card .skillContainer{width:75%;float:right;margin:0}}.card .skillContainer span{display:inline-block;min-width:5em;height:1.5em;color:yellow;background-color:#050586;border-radius:.5em;text-align:center;line-height:.1em;padding:1em;margin-top:1em}.card .skillContainer span+span{margin-left:.5em}

div{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}div .languageIcon{min-width:128px;min-height:128px;max-width:60vh;max-height:60vh;height:20vh;margin:.5em .5em}div .bigIcon{width:60vh;height:60vh}

.selfImgContainer{width:20em;height:20em;margin-top:5em;position:relative}@media only screen and (min-width: 566px){.selfImgContainer{width:30em;height:30em;margin-top:5em}}@media only screen and (min-width: 991px){.selfImgContainer{width:40em;height:40em;margin-top:5em}}@media only screen and (min-width: 1305px){.selfImgContainer{width:40em;height:40em;margin-top:5em}}@media only screen and (min-width: 1470px){.selfImgContainer{width:30em;height:30em;margin-top:0}}@media only screen and (min-width: 1540px){.selfImgContainer{width:40em;height:40em;margin-top:0}}.selfImgContainer>div{position:absolute;width:20em;height:20em;overflow:hidden;border-radius:50%;border:2em solid gray}@media only screen and (min-width: 566px){.selfImgContainer>div{width:30em;height:30em}}@media only screen and (min-width: 991px){.selfImgContainer>div{width:40em;height:40em}}@media only screen and (min-width: 1305px){.selfImgContainer>div{width:40em;height:40em}}@media only screen and (min-width: 1470px){.selfImgContainer>div{width:30em;height:30em}}@media only screen and (min-width: 1540px){.selfImgContainer>div{width:40em;height:40em}}.selfImgContainer>div>img{width:100%;height:auto}.selfImgContainer .hexagon{position:absolute;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.selfImgContainer .hexagon.topleft{top:calc(50% - 50% / 1.414);left:calc(50% - 50% / 1.414)}.selfImgContainer .hexagon.topcenter{top:0;left:50%;transform:translateX(-50%) translateY(15%)}.selfImgContainer .hexagon.topright{top:calc(50% - 50% / 1.414);right:calc(50% - 50% / 1.414)}.selfImgContainer .hexagon.middleleft{top:50%;left:0;transform:translateX(20%)}.selfImgContainer .hexagon.middleright{top:50%;right:0;transform:translateX(-20%)}.selfImgContainer .hexagon.bottomleft{bottom:calc(50% - 50% / 1.414);left:calc(50% - 50% / 1.414)}.selfImgContainer .hexagon.bottomcenter{bottom:0;left:50%;transform:translateX(-50%) translateY(-15%)}.selfImgContainer .hexagon.bottomright{bottom:calc(50% - 50% / 1.414);right:calc(50% - 50% / 1.414)}

.swimmerLine{width:100%;height:5vh;position:fixed;top:1%;background-color:transparent;overflow:hidden;z-index:1}.swimmerLine .swimmer{width:5vh;height:100%;background-repeat:no-repeat;background-size:contain;position:absolute;right:0;transform:translateX(100%)}.swimmerLine .swimmer.stretchArm{background-image:url(data:image/png;base64,)}.swimmerLine .swimmer.bendArm{background-image:url(data:image/png;base64,)}.swimmerLine .swimmer.inWaterArm{background-image:url(data:image/png;base64,)}.swimmerLine .swimmer.goldMedal{background-image:url(data:image/png;base64,)}

.poolLineContainer{width:100vw;height:100vh;position:relative;top:0;background-color:transparent;display:flex;flex-direction:row;justify-content:space-evenly;z-index:0}.poolLineContainer .line{width:1vw;height:100vh;display:flex;flex-direction:column;justify-content:space-evenly;z-index:0}.poolLineContainer .line .brick{width:1vw;height:2vh;background-color:#FF595C;z-index:0}.poolLineContainer .person{position:absolute}.poolLineContainer .person.one{top:20%;left:18%}.poolLineContainer .person.two{top:80%;left:28%;transform:rotate(180deg)}.poolLineContainer .person.three{top:45%;left:35%;transform:rotate(180deg)}.poolLineContainer .person.four{top:30%;left:45%}.poolLineContainer .person.five{top:60%;left:58%}.poolLineContainer .person.six{top:5%;left:70%}

